<template>
<div>
  <body class="section test adminview">
    <div class="grid-x grid-padding-x">
      <!-- HEADER -->
      <div class="small-12">
        <div class="grid-x grid-padding-x">
          <div class="cell small-12 title">
            <h1>Display Test</h1>
          </div>
        </div>
      </div>

      <div class="grid-container">
        <div class="grid-x grid-margin-x">
          <div class="main-content text-center">
            <p
              class="message-large"
            >A Red, Green and Blue color will be displayed here in a random sequence</p>

            <div class="grid-container">
              <div class="grid-x grid-margin-x">
                <div class="cell small-3">
                  <div v-bind:style="{backgroundColor: randomColor()}" class="circle-colors"></div>
                </div>
                <div class="cell small-9">
                  <div class="cell small-12 select-title">
                    <p>Select the color you see in the above box</p>
                  </div>
                  <div class="cell small-12">
                    <div class="grid-x grid-padding">
                      <a
                        onclick="emitData('displayTestCompleted',2)"
                        class="cell small-3 small-push-2 select blue"
                      >
                        <div class="blue">Blue</div>
                      </a>
                      <a
                        onclick="emitData('displayTestCompleted',0)"
                        class="cell small-3 small-push-2 select green"
                      >
                        <div class="red">Red</div>
                      </a>
                      <a
                        onclick="emitData('displayTestCompleted',1)"
                        class="cell small-3 small-push-2 select red"
                      >
                        <div class="green">Green</div>
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </body>
</div>
</template>

<script>
export default {
  name: "testDisplay",
  props: {
    msg: String
  },
  methods: {
    randomColor: function() {
      var colors = ["#ff0000", "#00ff00", "#0000ff"];
      var randomNumber = Math.floor(Math.random() * colors.length);
      var displayColor = colors[randomNumber];
      var EventEmitter = require("redis-events").EventEmitter;
      var emitter = EventEmitter();
      emitter.emit("displayTestColor", randomNumber);
      return displayColor;
      console.log(displayColor);
    }
  }
};
</script>